<template>
    <input :class="[error ? 'inp-danger' : '', 'inp']" @input="updateInput" :value="modelValue" type="number" placeholder="Введите данные">
</template>

<script>
export default {
    name:'inp-standard',
    props:{
        modelValue:[String, Number],
        error:{
            type:Boolean,
            default:false,
        }
    },
    methods:{
        updateInput(event){
            this.$emit("update:modelValue",event.target.value)
        }
    }
}
</script>

<style scoped>
.inp {
    width: 100%;
    border: 1px solid #DFE3E6;
    border-radius: 3px;
    font-weight: 400;
    color: #000;
    font-size: 14px;
    padding: 8px 10px;
    font-family: 'LabGrotesqueRegular', sans-serif;
    margin: 8px 0;
}


.inp-danger {
    border: 1px solid #EA0029;
}

</style>
