                fill="#EA0029" />
        </svg>
    </div>
    <h1>Налоговый вычет</h1>
    <p class="sub-head">Используйте налоговый вычет чтобы погасить ипотеку досрочно. Размер налогового вычета составляет не более 13% от своего официального годового дохода.</p>
    <p class="sup-head">Ваша зарплата в месяц</p>
    <inp-standard :error="error" v-model="salary" placeholder="Введите данные" />
    <btn-custom-inline  @click="calculate">Рассчитать</btn-custom-inline>
    <div style="margin-top:16px;overflow: auto;max-height: 208px;">
        <ListItems  :optionSalary="optionSalary"/>
    </div>
    <div class="supper-head">
        <p class="sup-head">Что уменьшаем?</p>
        <div class="supper-head-btns">
            <btn-default :class="['btn-round']">Платёж</btn-default>
            <btn-default :class="['btn-round disabled-btn']">Срок</btn-default>
        </div>
    </div>
    <btn-default style="width:100%;margin-top:40px;">Добавить</btn-default>
</div>
</template>

<script>
import ListItems from '@/components/ListItems'
export default {
    components:{
        ListItems
    },
    props:{
        show:{
            type:Boolean,
            default:false
        }
    },
    data() {
        return {
            salary:0,
            error:false,
            fault:0,
            deduction:0,
            optionSalary: [],
        }
    },
    methods: {
        hideDialog(){
            this.$emit("update:show",false)
        },
        calculate() {
            this.optionSalary = []
            this.fault =  0
            this.error = false
            this.deduction = Math.ceil((this.salary * 12) * 0.13)
            if (this.deduction < 260000 && this.salary > 10000) {
                while (this.fault + this.deduction < 260000) {
                    this.fault += this.deduction
                    this.optionSalary.push({
                        salary: this.deduction
                    })
                }
                this.optionSalary.push({
                    salary: 260000 - this.fault
                })
            } else if(this.deduction  >  260000 && this.salary > 10000)
                this.optionSalary.push({
                    salary: 260000
                })
            else
                this.error = true

        }
    }
}
</script>

<style scoped>
