    margin: 26px 0 24px 0;
    color: #808080;
    font-size: 14px;
}



.sup-head {
    color: #000;
    font-size: 14px;
    font-family: "LabGrotesqueMedium";
}




.modal {
    background: #FFFFFF;
    border-radius: 30px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 32px;
    width: 552px;
}

